import {
  Coin,
  Coins,
  Extension,
  LCDClient,
  MsgExecuteContract,
} from '@terra-money/terra.js';
import { networks } from 'controllers/networks';
import { ExtensionRequests, ExtensionResponse } from 'typedefs/extension';
import {
  Token,
  NativeToken,
  Pair,
  Swap,
} from 'typedefs/protocol';
import {
  PairsMap,
  NetworkType,
  Network,
} from 'typedefs/common';

export const terraClientByNetwork: Record<NetworkType, LCDClient> = {
  [NetworkType.Mainnet]: new LCDClient({
    URL: networks.mainnet.lcd,
    chainID: networks.mainnet.chainID,
  }),
  [NetworkType.Testnet]: new LCDClient({
    URL: networks.testnet.lcd,
    chainID: networks.testnet.chainID,
  }),
};

export const extension = new Extension();

export const connectWallet = async () => {
  const { payload: { address } } = await extension.request(
    ExtensionRequests.Connect,
  ) as ExtensionResponse<{ address: string }>;

  const { payload: { name: networkType } } = await extension.request(
    ExtensionRequests.Info,
  ) as ExtensionResponse<{ name: NetworkType }>;

  return {
    address,
    network: networks[networkType],
  };
};

export const requestBalance = (
  { name }: Network,
  address: string,
) => (
  terraClientByNetwork[name].bank.balance(address)
);

const isNativeToken = (
  token: Token | NativeToken,
): token is NativeToken => {
  if ('native_token' in token) {
    return true;
  }

  return false;
};

const addPair = (
  pairsMap: PairsMap,
  pair: Pair,
  from: Token | NativeToken,
  to: Token | NativeToken,
) => {
  const [tokenFrom, tokenTo] = [from, to].map((token) => (
    isNativeToken(token)
      ? token.native_token.denom
      : token.token.contract_addr
  ));

  const prevPairs = pairsMap[tokenFrom] || {};

  return {
    ...pairsMap,
    [tokenFrom]: {
      ...prevPairs,
      [tokenTo]: pair,
    },
  };
};

const makePairsMap = (pairs: Pair[]): PairsMap => (
  pairs.reduce<PairsMap>((pairsMap, pair) => {
    const [firstToken, secondToken] = pair.asset_infos;

    return {
      ...addPair(pairsMap, pair, firstToken, secondToken),
      ...addPair(pairsMap, pair, secondToken, firstToken),
    };
  }, {})
);

const maxPairsLimit = 30;

export const requestPairs = async (
  network: Network,
): Promise<PairsMap> => {
  const client = terraClientByNetwork[network.name];

  const request = async (
    lastPairs: Pair[] = [],
  ): Promise<Pair[]> => {
    const lastPair = lastPairs[lastPairs.length - 1];

    const response: { pairs: Pair[] } = await client.wasm
      .contractQuery(network.factory, {
        pairs: {
          start_after: lastPair && lastPair.asset_infos,
          limit: maxPairsLimit,
        },
      });

    if (response.pairs.length < maxPairsLimit) {
      return [...lastPairs, ...response.pairs];
    }

    return request([...lastPairs, ...response.pairs]);
  };

  const pairs = await request();

  return makePairsMap(pairs);
};

export const swapTokens = async (
  sender: string,
  pair: Pair,
  from: string,
  amount: string,
) => {
  const {
    contract_addr,
  } = pair;

  const executeMsg: Swap = {
    swap: {
      offer_asset: {
        info: {
          native_token: { // TODO: add another token
            denom: from,
          },
        },
        amount,
      },
    },
  };

  const msg = new MsgExecuteContract(
    sender,
    contract_addr,
    executeMsg,
    new Coins([new Coin(from, amount)]),
  );

  const { payload } = await extension.request(
    ExtensionRequests.Post,
    {
      msgs: [msg.toJSON()],
    },
  ) as ExtensionResponse<any>; // TODO: add type for post

  return payload;
};
