import { Pair } from 'typedefs/protocol';

export enum NetworkType {
  Mainnet = 'mainnet',
  Testnet = 'testnet',
}
export interface Network {
  name: NetworkType,
  chainID: string
  lcd: string,
  mantle: string,
  factory: string
}

export type Networks = Record<NetworkType, Network>

export interface PairsMap {
  [from: string]: {
    [to: string]: Pair
  }
}
