import { FC, memo, useMemo } from 'react';
import cn from 'classnames';
import style from 'components/Wallet/Wallet.module.scss';
import { WalletIcon } from 'components/ui/icons/WalletIcon';
import { Button } from 'components/ui/Button/Button';
import { sliceStringFromTo } from 'components/helpers';
import { FinderButton } from 'components/ui/buttons/FinderButton';
import { CopyButton } from 'components/ui/buttons/CopyButton';
import { useTerra } from 'contexts/TerraContext';
import { SendIcon } from 'components/ui/icons/SendIcon';

interface Props {
  address: string
  balance: string | null
}

export const Wallet: FC<Props> = memo((props) => {
  const {
    balance,
    address,
  } = props;

  const { destroy } = useTerra();

  const slicedAddress = useMemo(() => sliceStringFromTo(
    address, 16, -16,
  ), [address]);

  return (
    <div className={style.wallet}>
      <div className={style.wrapper}>
        <WalletIcon
          className="mr-20"
          width="26"
        />

        <span className={style.balance}>
          {balance}
        </span>

        <Button
          onClick={destroy}
          mode={Button.mode.Secondary}
        >
          Disconnect
        </Button>
      </div>

      <p className={style.addressTitle}>
        your address
      </p>

      <p className={style.address}>
        {slicedAddress}
      </p>

      <div className={cn(style.wrapper, 'mt-16')}>
        <div className="mr-36">
          <CopyButton
            target={address}
          >
            Copy Address
          </CopyButton>
        </div>

        <FinderButton
          findBy={FinderButton.findBy.Address}
          address={address}
        >
          View on Terra Finder
        </FinderButton>
      </div>

      <Button className="mt-16">
        <SendIcon
          width="18"
          height="16"
        />
        <div className="mr-16">
          Send
        </div>
      </Button>
    </div>
  );
});
