import { useMemo, useRef } from 'react';
import { Denom } from '@terra-money/terra.js';
import { useTerra } from 'contexts/TerraContext';
import { ConnectButton } from 'components/Wallet/ConnectButton';
import { WalletButton } from 'components/Wallet/WalletButton';
import { Wallet } from 'components/Wallet/Wallet';
import { useToggleVisibility } from 'hooks/useToggleVisibility';
import { formatBalance } from 'components/Wallet/helpers';
import { readLocalStorage } from 'components/helpers';

export const WalletContainer = () => {
  const {
    address,
    balance,
  } = useTerra();

  const walletEl = useRef<HTMLDivElement>(null);

  const [
    isWalletVisible,
    toggleWalletVisibility,
  ] = useToggleVisibility({
    handlingClickOutside: true,
    targetElement: walletEl,
  });

  const uusdBalance = useMemo(() => {
    // TODO: move to hook or helpers
    if (!balance) {
      return null;
    }

    const uusd = balance.get(Denom.USD);
    const formattedBalance = formatBalance(uusd);

    return `UST ${formattedBalance}`;
  }, [balance]);

  const connected = readLocalStorage<boolean>('connected');

  if (!(address || connected)) {
    return <ConnectButton />;
  }

  return (
    <>
      <WalletButton
        address={address}
        balance={uusdBalance}
        onClick={toggleWalletVisibility}
      />
      {isWalletVisible && address && (
        <div ref={walletEl}>
          <Wallet
            address={address}
            balance={uusdBalance}
          />
        </div>
      )}
    </>
  );
};
