import { FC, memo, useMemo } from 'react';
import { sliceStringFromTo } from 'components/helpers';
import { Button } from 'components/ui/Button/Button';
import { WalletIcon } from 'components/ui/icons/WalletIcon';
import style from 'components/Wallet/Wallet.module.scss';

interface Props {
  address: string | null
  onClick: () => void
  balance: string | null
}

export const WalletButton: FC<Props> = memo((props) => {
  const {
    address,
    balance,
    onClick,
  } = props;

  const slicedAddress = useMemo(() => (
    address ? sliceStringFromTo(address, 2, -4) : ''
  ), [address]);

  return (
    <Button
      onClick={onClick}
      className={style.walletBtn}
      size={Button.size.Large}
    >
      <div className={style.walletBtnInner}>
        <div className={style.walletBtnAddress}>
          <WalletIcon
            width="18"
            height="16"
          />
          {slicedAddress}
        </div>
        <div className={style.walletBtnBalance}>
          {balance}
        </div>
      </div>
    </Button>
  );
});
