import { FC, useCallback, useState } from 'react';
import cn from 'classnames';
import { Button } from 'components/ui/Button/Button';
import { CopyIcon } from 'components/ui/icons/CopyIcon';
import style from 'components/ui/buttons/buttons.module.scss';

interface Props {
  target: string
}

export const CopyButton: FC<Props> = (props) => {
  const { target, children } = props;

  const [isMouseDown, setIsMouseDown] = useState(false);

  const copy = useCallback(() => {
    navigator.clipboard.writeText(target);
  }, [target]);

  const toggleMouseDown = useCallback(() => {
    setIsMouseDown((prev) => !prev);
  }, []);

  return (
    <Button
      className={cn(style.copyBtn, {
        [style.copyBtnActive]: isMouseDown,
      })}
      mode={Button.mode.Simple}
      onClick={copy}
      onMouseDown={toggleMouseDown}
      onMouseUp={toggleMouseDown}
    >
      <CopyIcon
        width="16"
        height="16"
        fill="black"
      />
      {children}
    </Button>
  );
};
