import { FC } from 'react';
import { Button } from 'components/ui/Button/Button';
import { ViewIcon } from 'components/ui/icons/ViewIcon';
import { useTerra } from 'contexts/TerraContext';
import { FINDER } from '../../constants';

enum FinderType {
  Address = 'address'
}

interface Props {
  findBy: FinderType,
  address: string,
}

type ButtonType = FC<Props> & {
  findBy: typeof FinderType
}

export const FinderButton: ButtonType = (props) => {
  const {
    address,
    findBy,
    children,
  } = props;

  const { network } = useTerra();

  return (
    <Button
      mode={Button.mode.Simple}
      href={`${FINDER}/${network.chainID}/${findBy}/${address}`}
      target="_blank"
      rel="noopener noreferrer"
    >
      <ViewIcon
        width="14"
        height="14"
      />
      {children}
    </Button>
  );
};

FinderButton.findBy = FinderType;
