import { Component } from 'react';
import { Coins } from '@terra-money/terra.js';
import { Network, NetworkType, PairsMap } from 'typedefs/common';
import {
  connectWallet,
  requestBalance,
  requestPairs,
  swapTokens,
} from 'components/Terra/helpers';
import { TerraProvider } from 'contexts/TerraContext';
import { networks } from 'controllers/networks';
import { readLocalStorage, writeLocalStorage } from 'components/helpers';

export type SwapToken = (
  sender: string,
  from: string,
  to: string,
  amount: string,
) => Promise<any>

type Props = {
  children: React.ReactNode
};

interface State {
  network: Network
  address: string | null
  balance: Coins | null
  pairs: PairsMap
}

export class Terra extends Component<Props, State> {
  defaultNetwork = networks[NetworkType.Mainnet]

  connectWallet = connectWallet

  requestBalance = requestBalance

  requestPairs = requestPairs

  swapTokens = swapTokens

  readLocalStorage = readLocalStorage

  writeLocalStorage = writeLocalStorage

  state: State = {
    network: this.defaultNetwork,
    address: null,
    balance: null,
    pairs: {},
  }

  componentDidMount() {
    const connected = this.readLocalStorage<boolean>('connected');

    if (connected) {
      this.connect();
    }

    this.fetchPairs();
  }

  componentDidUpdate(_: Props, prevState: State) {
    const {
      address,
      network,
    } = this.state;

    if (prevState.address !== address) {
      this.fetchBalance();
    }

    if (prevState.network.name !== network.name) {
      this.fetchPairs();
    }
  }

  connect = async () => {
    const { address, network } = await this.connectWallet();

    this.writeLocalStorage('connected', true);
    this.setState({ address, network });
  }

  destroy = () => {
    this.writeLocalStorage('connected', false);
    this.setState({
      network: this.defaultNetwork,
      address: null,
      balance: null,
    });
  }

  async fetchBalance() {
    const { network, address } = this.state;

    if (!address) {
      return;
    }

    const balance = await this.requestBalance(network, address);

    this.setState({ balance });
  }

  async fetchPairs() {
    const { network } = this.state;

    const pairs = await this.requestPairs(network);

    if (network !== this.state.network) {
      // TODO: add explanation
      return;
    }

    this.setState({ pairs });
  }

  swap: SwapToken = async (sender, from, to, amount) => {
    const response = await this.swapTokens(
      sender,
      this.state.pairs[from][to],
      from,
      amount,
    );

    return response;
  }

  render() {
    const {
      network,
      address,
      balance,
      pairs,
    } = this.state;

    const {
      connect,
      destroy,
      swap,
    } = this;

    return (
      <TerraProvider
        value={{
          connect,
          destroy,
          network,
          address,
          balance,
          pairs,
          swap,
        }}
      >
        {this.props.children}
      </TerraProvider>
    );
  }
}
