import { createContext, useContext } from 'react';
import { Coins } from '@terra-money/terra.js';
import { Network, PairsMap } from 'typedefs/common';
import { SwapToken } from 'components/Terra/Terra';

type TerraContextType = {
  connect: () => void
  destroy: () => void
  network: Network
  address: string | null
  balance: Coins | null
  pairs: PairsMap
  swap: SwapToken
}

export const TerraContext = (
  createContext<TerraContextType | undefined>(undefined)
);

export const TerraProvider = TerraContext.Provider;

export const useTerra = () => {
  const c = useContext(TerraContext);

  if (!c) {
    throw new Error(`useTerra must be inside a Provider with a value`);
  }

  return c;
};
