import { FC } from 'react';
import { NavLink } from 'react-router-dom';
import style from 'components/Header/Header.module.scss';

interface Props {
  to: string
}

export const NavigationLink: FC<Props> = (props) => {
  const {
    children,
    to,
  } = props;

  return (
    <NavLink
      className={style.navLink}
      to={to}
      exact
    >
      {children}
    </NavLink>
  );
};
