import {
  AnchorHTMLAttributes,
  ButtonHTMLAttributes,
  FC,
  memo,
} from 'react';
import cn from 'classnames';
import style from 'components/ui/Button/Button.module.scss';

enum ButtonMode {
  Primary = 'primary',
  Secondary = 'secondary',
  Border = 'border',
  Lite = 'lite',
  Simple = 'simple',
}

enum ButtonSize {
  Small = 'small',
  Medium = 'medium',
  Large = 'large',
}

const modeClassNames: Record<ButtonMode, string> = {
  [ButtonMode.Primary]: style.primary,
  [ButtonMode.Secondary]: style.secondary,
  [ButtonMode.Border]: style.border,
  [ButtonMode.Lite]: style.lite,
  [ButtonMode.Simple]: style.simple,
};

const sizeClassNames: Record<ButtonSize, string> = {
  [ButtonSize.Small]: style.small,
  [ButtonSize.Medium]: style.medium,
  [ButtonSize.Large]: style.large,
};

interface CustomProps {
  mode?: ButtonMode
  size?: ButtonSize
}

type Props = CustomProps
  & ButtonHTMLAttributes<HTMLButtonElement>
  & AnchorHTMLAttributes<HTMLAnchorElement>

type ButtonType = FC<Props> & {
  mode: typeof ButtonMode;
  size: typeof ButtonSize;
};

export const Button: ButtonType = memo((props) => {
  const {
    mode = ButtonMode.Primary,
    size = ButtonSize.Medium,
    className,
    children,
    // Anchor props
    download,
    href,
    hrefLang,
    media,
    ping,
    rel,
    target,
    referrerPolicy,
    // Button props
    form,
    formAction,
    formEncType,
    formMethod,
    formNoValidate,
    formTarget,
    name,
    value,
    disabled,
    type,
    // Rest props
    ...rest
  } = props;

  const rootClassName = cn(
    style.baseButton,
    modeClassNames[mode],
    { [sizeClassNames[size]]: mode !== ButtonMode.Simple },
    className,
  );

  return href ? (
    <a
      className={rootClassName}
      download={download}
      href={href}
      hrefLang={hrefLang}
      media={media}
      ping={ping}
      rel={rel}
      target={target}
      referrerPolicy={referrerPolicy}
      {...rest}
    >
      {children}
    </a>
  ) : (
    <button
      type={type}
      disabled={disabled}
      className={rootClassName}
      form={form}
      formAction={formAction}
      formEncType={formEncType}
      formMethod={formMethod}
      formNoValidate={formNoValidate}
      formTarget={formTarget}
      name={name}
      value={value}
      {...rest}
    >
      {children}
    </button>
  );
}) as FC<Props> as ButtonType;

Button.mode = ButtonMode;
Button.size = ButtonSize;
