import { Coin } from '@terra-money/terra.js';

export const formatBalance = (balance: Coin | undefined): string => {
  if (!balance) {
    return '0';
  }

  const amount = Math.floor(balance.amount.toNumber() / 1000000);

  return `${amount > 1000 ? amount / 1000 : amount}${amount > 1000 ? ' K' : ''}`;
};
