import { useTerra } from 'contexts/TerraContext';
import { ChromeButton } from 'components/ui/buttons/ChromeButton';
import { ConnectWalletButton } from 'components/ui/buttons/ConnectWalletButton';
import { ExtButton } from 'components/ui/buttons/ExtButton';

const getButton = () => {
  if (!navigator.userAgent.includes('Chrome')) {
    return ChromeButton;
  }

  if (!window.isTerraExtensionAvailable) {
    return ExtButton;
  }

  return ConnectWalletButton;
};

export const ConnectButton = () => {
  const { connect } = useTerra();
  const Button = getButton();

  return <Button connect={connect} />;
};
